@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&family=Lobster&family=Pacifico&display=swap);
*{font-family:"Baloo Tamma 2", cursive}#app{background-color:#fff;margin:1rem}.p-0{padding:0px !important}.contrast-60{-webkit-filter:contrast(60%);filter:contrast(60%);background-image:linear-gradient(black, black);background-size:cover;background-blend-mode:saturation}.ant-btn-primary{background-color:#2f5a78}.menu{width:100%;padding:0 20px}.page-skeleton{margin-top:2rem}.spinner-container{height:90vh;display:flex;justify-content:center;align-items:center}.app-header{display:flex;box-shadow:0 2px 8px #f0f1f2;padding:0}.ant-divider{border-top:1px solid rgba(0,0,0,0.2)}.evenly{display:flex;background-color:red;justify-content:space-around}.box{box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12)}.menu-bar-container{display:grid}.menu-bar-container .menu-bar{height:80px;display:flex;border-radius:10px;box-shadow:5px 10px 90px #ccc;justify-content:space-between;background-color:white;width:97%;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);align-items:center}.menu-bar-container .menu-bar .logo{display:flex;align-items:center;margin-top:1rem}.menu-bar-container .menu-bar .logo__img{height:90px;width:90px}.menu-bar-container .menu-bar .logo__img:hover{opacity:0.5}.menu-bar-container .menu-bar .menu-right{display:flex;line-height:10px}.menu-bar-container .menu-bar .submenu-right{display:flex;justify-content:flex-end;list-style:none;color:black;font-weight:bold;position:relative;cursor:pointer;white-space:nowrap;background-color:transparent}.menu-bar-container .menu-bar .submenu-right a{padding:0px !important}.menu-bar-container .menu-bar .submenu-right.__button{color:white;background-color:#251f44;border-radius:10px}.menu-bar-container .menu-bar .submenu-right::before{content:" ";position:absolute;top:0;left:0;height:100%;width:100%;z-index:-1;transition:0.2s;border-radius:5px}.menu-bar-container .menu-bar .submenu-right:hover::before{scale:2}

.listing-card{width:50%}.listing-card__cover-img{height:15rem;background-size:cover}.listing-card__guest-container{display:flex;align-items:center;justify-content:flex-end}.listing-card__guest-container .svg{line-height:1;padding-right:5px}

