.listing{margin:2rem}.listing-create{display:flex;justify-content:center;width:100%}.listing-create .ant-typography{margin-bottom:0px !important}.listing-create__card .ant-card-body{display:flex;background-color:white !important;flex-direction:column;align-items:center;justify-content:center}.listing-button{border-radius:5px}.listing-details__image{height:34rem;width:100%;max-height:50%;border-radius:5px;background-size:cover}.listing-details__information{margin-top:2rem}.listing-details__information-location{font-size:18px;display:flex;align-items:center}.listing-details__information-location .anticon{padding-right:5px;line-height:1;font-size:15px}.listing-details__information-address{font-size:18px;width:75%;margin-bottom:0px !important}.listing-details__title .ant-typography{font-weight:bold;font-size:2rem}.listing-details__host{display:flex;align-items:center}.listing-details__host .ant-typography{margin-bottom:0px !important;font-weight:100;font-family:"Pacifico", cursive !important}.listing-details__section-description{margin-top:10px;border-radius:10px;box-shadow:5px 10px 90px #ccc;min-height:100px;padding:20px}

