.home{padding:20px}.home__location{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:4rem}.home__location-btn{padding:10px;border-radius:10px;background-color:aliceblue}.home__skeleton{width:100%}.home__skeleton-image{height:200px;width:100%;background-color:transparent}.home__another-image{height:350px;width:100%;background-position:center center;background-repeat:no-repeat;background-size:cover;border-radius:2px}.home__another-image-title{position:absolute;color:#fff !important;top:0;width:100%;text-align:center;font-size:1.5rem !important;font-weight:100 !important;display:flex;flex-wrap:wrap;height:100%;justify-content:center;align-items:center;z-index:99}

