.google-text{font-size:12px}.log-in{margin-top:2rem}.ant-card{background-color:#f3f3f3;border-radius:10px;box-shadow:10px 10px 50px #a09f9f}.singInButton{display:inline-block;font-size:1.5em;text-decoration:none;color:white;padding:0.55em 0.3em;clear:both;cursor:pointer;border:none;text-align:center;margin:0.5em 0.1em}.btn{display:block;color:#fff;text-decoration:none;margin:20px 0;padding:15px 15px;border-radius:5px;position:relative}.btn::after{content:"";position:absolute;z-index:1;top:0;left:0;width:100%;height:100%;transition:all 0.2s ease-in-out;box-shadow:inset 0 3px 0 rgba(0,0,0,0),0 3px 3px rgba(0,0,0,0.2);border-radius:5px}.btn:hover::after{background:rgba(0,0,0,0.1);box-shadow:inset 0 3px 0 rgba(0,0,0,0.2)}.btn-social{padding-left:64px;position:relative;z-index:1}.btn-social:hover{color:white}.btn-social .fa{position:absolute;top:0;left:0;z-index:1;width:50px;height:100%;text-align:center;background:rgba(0,0,0,0.1);line-height:3.2;border-radius:5px 0 0 5px}.btn-google{background-color:#de4e3b}

